.intro {
    height: 100%;
    width: 100%;
    margin: auto;
    display: table;
    font-family: 'Exo 2', sans-serif;
    background: #111112;
    background: -webkit-linear-gradient(90deg,#111112,#232526);
    background: -webkit-linear-gradient(left,#111112,#232526);
    background: linear-gradient(90deg,#111112,#232526);
    border-bottom: 5px solid #00af83;
    outline: none;
}

.intro .inner {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
}

.content {
    width: 80%;
    height: auto;
    margin: 0 auto;
    text-align: center;

}

.content h1 {
    font-family: 'Baron Neue',sans-serif;
    font-size: 4rem;
    color: #00af83;
    margin-top: 8rem;
}

.content h1 i {
    color: #00af83;
    font-size: 6rem;
    width: 100%;
}

.content h2 {
    margin: 2.5rem 0 6rem 0;
    font-weight: 100;
}

.skills-container {
    display: flex;
    flex-flow: row wrap;
    width: 80%;
    align-content: center;
    justify-content: space-evenly;
    margin: 1.8rem auto;
}

.skills-container .skill-cards {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px;
    box-sizing: border-box;
    min-width: 100px;
}

.skills-container .skill-cards:hover *{
    color: #00af83;
}

.skills-container .skill-cards i {
    font-size: 3rem;
    -webkit-transition: all ease 300ms;
    transition: all ease 300ms;
}

.skills-container .skill-cards h3 {
    font-size: 1.2rem;
    font-weight: 100;
    margin: auto;
    -webkit-transition: all ease 300ms;
    transition: all ease 300ms;
}

.arrow.bounce a { 
    color: rgb(216, 216, 216); 
    text-decoration: none;
    -webkit-transition: color ease 300ms;
    transition: color ease 300ms;
}

.arrow.bounce a:hover { 
    color: #00af83; 
}

.arrow {
    text-align: center;
    padding: 7% 0;
}
.bounce {
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
}
  
@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
}
  
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
}

