    margin: 6rem 0 0 0;
    padding: 0 0 2rem 0;
    text-align: center;
    position: relative;
}

.projects-body h1:after {
    content: '';
    position: absolute;
    width: 80px;
    height: 5px;
    margin-left: -40px;
    left: 50%;
    bottom: 0px;
    background-color: #00af83;
}

.projects-container {
    display: flex;
    flex-flow:  wrap;
    width: 80%;
    margin: 3rem auto 6rem auto;
}

.projects-container h2 {
    color: #0b0b0c;
}
.project-card {
    display: flex;
    flex-direction: column;
    margin: 1rem auto;
    box-shadow: 10px 10px 31px -9px rgba(0,0,0,0.6);
    margin-bottom: 1.6%;
    background: #fff;
    line-height: 1.4;
    font-family: sans-serif;
    border-radius: 5px;
    overflow: hidden;
    z-index: 0;
}

.project-card a {
    color: inherit;
    text-decoration: none;
}

.project-card a:hover {
    color: #00af83;
}

.project-card:hover .photo {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
                -webkit-filter: blur(2.5px);
                        filter: blur(2.5px)
}

.project-card .meta {
    position: relative;
    z-index: 0;
    height: 200px;
}

.project-card .photo {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    transition: -webkit-transform .2s;
    -webkit-transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.project-card .details,
.project-card .details ul {
    font-family: 'Exo 2', sans-serif;
    margin: auto;
    padding: 0;
    list-style: none;
    font-size: 15px
}

.project-card .details {
    position: absolute;
    top: 0;
    bottom: 0;
    left: -110%;
    margin: auto;
    -webkit-transition: left .2s;
    transition: left .2s;
    background: rgba(0, 0, 0, 0.6);
    color: #fff;
    padding: 10px;
    width: 100%;
    font-size: .9rem;
}


.prject-card .details .tags li {
    margin-right: 2px;
}


.project-card .description {
    padding: 1rem;
    background: #fff;
    position: relative;
    z-index: 1;
}

.project-card .description h1,
.project-card .description h2 {
    font-family: 'Exo 2', sans-serif;
}

.project-card .description h2 {
    line-height: 1;
    margin: 0;
    font-size: 1.7rem;
}


.project-card .description .project-links {
    text-align: right;
}

.project-card .description .project-links a {
    color: #00af83;
    display: inline-block;
    position: relative;
    margin: 0 5px;
    padding: 7px;
    border: #00af83 2px solid;
    border-radius: 7px;
}

.project-card .description .project-links .disabled {
    opacity: 0.35; 
    cursor: not-allowed;
}


.project-card p {
    position: relative;
    margin-top: 4rem;
    display: block;
    max-height: 4rem;
}

.project-card p:first-of-type {
    margin-top: 1.25rem;
}

.project-card p:first-of-type:before {
    content: "";
    position: absolute;
    height: 5px;
    background: #00af83;
    width: 35px;
    top: -0.75rem;
}

.project-card:hover .details {
    left: 0%;
}

@media (min-width: 640px) {
    .project-card {
        flex-direction: row;
        max-width: 600px;
    }
    .project-card .meta {
        flex-basis: 40%;
        height: auto;
    }
    .project-card .description {
        flex-basis: 60%;
    }
    .project-card .description:before {
        -webkit-transform: skewX(-3deg);
                transform: skewX(-3deg);
        content: "";
        background: #fff;
        width: 30px;
        position: absolute;
        left: -10px;
        top: 0;
        bottom: 0;
        z-index: -1;
    }
    .project-card.alt {
        flex-direction: row-reverse;
    }
    .project-card.alt .description:before {
        left: inherit;
        right: -10px;
        -webkit-transform: skew(3deg);
                transform: skew(3deg);
    }
    .project-card.alt .details {
        padding-left: 25px;
    }
    .project-card p {
        margin-top: 4rem;
    }
}

@media screen and (max-width: 768px){
    .project-card p {
        margin-top: 2rem;
    }
}
