.btn {
    display: inline-block;
    width: 90px;
    height: 90px;
    background: #f1f1f1;
    margin: 10px;
    border-radius: 30%;
    box-shadow: 0 8px 15px -5px black;
    color: #0b0b0c;
    overflow: hidden;
    position: relative;
}

.btn i {
    line-height: 90px;
    font-size: 2rem;
    -webkit-transition: 0.2s linear;
    transition: 0.2s linear;
}

.btn:hover i {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    color: #f1f1f1;
}

.btn::before {
    content: '';
    position: absolute;
    width: 120%;
    height: 120%;
    background: #00af83;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    left: -110%;
    top: 90%;
}

.btn:hover::before {
    -webkit-animation: bganim 0.7s 1;
            animation: bganim 0.7s 1;
    top: -10%;
    left: -10%;
}

